<style>
  
</style>

<template>
  <div>
    <div class="search-form-wraper">
      <el-form label-position="top">
        <el-form-item label="操作类型">
          <el-radio-group v-model="optionsType" size="small">
            <el-radio-button label="1">清数据</el-radio-button>
            <el-radio-button label="2">清DB</el-radio-button>
            <el-radio-button label="3">按key清数据</el-radio-button>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="key值" v-if="optionsType === '3'">
          <el-input v-model.trim="keys" size="small" class="search-input" placeholder="请输入key值" @keyup.enter.native="doClearHandel" clearable></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="doClearHandel" size="mini" :disabled="disabled" :loading="disabled">执行</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
  const requestUrl = {
    clearData: '/plateform/a/clear/data',
    clearDB: '/plateform/a/clear/db',
    clearKeys: '/plateform/a/clear/keys'
  }
  export default {
    data() {
      return {
        disabled: false,
        optionsType: '1',
        keys: ''
      }
    },
    created() {

    },
    mounted() {

    },
    methods: {
      doClearHandel() {
        if(this.optionsType === '1') {
          // 清数据
          this.disabled = true
          this.$http.post(requestUrl.clearData).then(res => {
            this.disabled = false
            if (res.code === 0) {
              this.$message.success("清数据执行成功")
              return
            }
            throw new Error(res.msg)
          }).catch(e => {
            this.disabled = false
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }else if(this.optionsType === '2') {
          // 清DB
          this.disabled = true
          this.$http.post(requestUrl.clearDB).then(res => {
            this.disabled = false
            if (res.code === 0) {
              this.$message.success("清DB执行成功")
              return
            }
            throw new Error(res.msg)
          }).catch(e => {
            this.disabled = false
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }else if(this.optionsType === '3') {
          if(!this.keys) {
            this.$message.warning("请输入key值")
            return
          }
          // 按key清数据
          let params = {
            keys: this.keys
          }
          this.disabled = true
          this.$http.post(requestUrl.clearKeys, {...params}).then(res => {
            this.disabled = false
            if (res.code === 0) {
              this.$message.success("按key清数据执行成功")
              return
            }
            throw new Error(res.msg)
          }).catch(e => {
            this.disabled = false
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }
      }
    }
    
  }
</script>


